import "core-js/modules/es.array.push.js";
import axios from 'axios';
import Bscroll from 'better-scroll';
import { useStore } from 'vuex';
import { ref, onMounted, watch, computed, nextTick } from 'vue';
import { useRouter } from 'vue-router';
export default {
  name: 'cityscontents',
  props: {
    letter: String,
    letters: Array
  },
  setup(props, context) {
    const hotcity = ref([]);
    const citys = ref([]);
    const router = useRouter();
    const store = useStore();
    const states = computed(() => store.state.city);
    const wrapper = ref(null);
    let scrolls = ref(null);
    const refarr = ref([]);
    const getcitys = () => {
      axios.get('/api/city.json').then(getcitysSucc);
    };
    const setitemref = el => {
      if (el) {
        refarr.value.push(el);
      }
    };
    const getcitysSucc = res => {
      hotcity.value = res.data.hotcity;
      citys.value = res.data.citys;
    };
    const toup = () => {
      router.go(-1);
    };
    const cityclick = city => {
      store.commit('changeCity', city);
      router.go(-1);
    };
    onMounted(() => {
      nextTick(() => {
        getcitys();
        scrolls = new Bscroll(wrapper.value, {
          click: true
        });
      });
    });
    watch(() => props.letter, () => {
      if (props.letter) {
        for (let i = 0; i < props.letters.length; i++) {
          if (props.letters[i] === props.letter) {
            const element = refarr.value[i];
            scrolls.scrollToElement(element);
          }
        }
      }
    });
    return {
      hotcity,
      citys,
      states,
      getcitys,
      getcitysSucc,
      toup,
      cityclick,
      wrapper,
      setitemref
    };
  }
};